import PropTypes from "prop-types";

export const InfoButton = ({ buttonText, active, onClick, ...props }) => {
  const classNames = [
    "rounded-lg capitalize border transition ease-in-out tracking-wide text-slate-50 hover:bg-slate-600 hover:border-slate-400 w-full",
    active ? "bg-slate-600 border-slate-400" : "bg-slate-700 border-slate-500",
  ].join(" ");

  return (
    <div className={classNames}>
      <button
        className="flex justify-between gap-4 px-5 py-2.5 w-full text-left"
        {...props}
        onClick={onClick}
      >
        <div className="-mr-4 inline-block">{buttonText}</div>
      </button>
    </div>
  );
};

InfoButton.propTypes = {
  buttonText: PropTypes.string,
  active: PropTypes.bool,
  onClick: PropTypes.func,
};
