import PropTypes from "prop-types";

export const InputField = ({
  label,
  name,
  type,
  value,
  rows,
  onChange,
  placeholder,
  error,
  className,
  inputClassName,
  icon,
  disabled,
  ...rest
}) => {
  const textarea = type === "textarea";
  const resolvedInputClassName = [
    "w-full",
    "text-slate-50 bg-slate-700 disabled:opacity-100 disabled:cursor-not-allowed",
    "outline outline-transparent",
    "rounded-lg",
    "outline-offset-0",
    textarea ? "h-auto" : "h-[50px]",
    "transition ease-in-out",
    "focus:outline-slate-500 hover:bg-slate-600",
    error ? "border border-red-500" : "",
    icon ? "pl-11 pr-4" : "px-4",
    textarea ? "py-3 max-h-96 min-h-[3rem]" : "",
    inputClassName,
  ].join(" ");

  let input;
  if (textarea) {
    input = (
      <textarea
        id={name}
        type={type}
        name={name}
        value={value || ""}
        rows={rows}
        onChange={onChange}
        placeholder={placeholder}
        className={resolvedInputClassName}
        disabled={disabled}
        {...rest}
      />
    );
  } else {
    input = (
      <input
        id={name}
        type={type}
        name={name}
        value={value || ""}
        onChange={onChange}
        placeholder={placeholder}
        className={resolvedInputClassName}
        disabled={disabled}
        {...rest}
      />
    );
  }

  return (
    <div className={`${className} grid gap-1`}>
      {label && <label htmlFor={name}>{label}</label>}
      <div className="relative w-full">{input}</div>
      {error && <p className="text-red-500 text-xs">{error}</p>}
    </div>
  );
};

InputField.propTypes = {
  name: PropTypes.string.isRequired,
  label: PropTypes.string,
  icon: PropTypes.string,
  type: PropTypes.string,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  onChange: PropTypes.func,
  placeholder: PropTypes.string,
  error: PropTypes.string,
  className: PropTypes.string,
  inputClassName: PropTypes.string,
  rows: PropTypes.number,
  disabled: PropTypes.bool,
};

InputField.defaultProps = {
  icon: null,
  type: "text",
  value: "",
  onChange: () => {},
  placeholder: "",
  error: "",
  className: "",
  inputClassName: "",
  rows: 5,
  disabled: false,
};
